import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useState } from 'react';
import {
  fetchContacts,
  addContact,
  deleteContact,
} from '../../redux/contacts/operations';
import {
  selectContacts,
  selectIsLoading,
  selectError,
} from '../../redux/contacts/selectors';
import {
  Box,
  Button,
  Container,
  FormControl,
  FormLabel,
  Input,
  VStack,
  Heading,
  Spinner,
  List,
  ListItem,
  IconButton,
  HStack,
  Text,
  useToast,
} from '@chakra-ui/react';
import { DeleteIcon } from '@chakra-ui/icons';

const ContactsPage = () => {
  const dispatch = useDispatch();
  const contacts = useSelector(selectContacts);
  const isLoading = useSelector(selectIsLoading);
  const error = useSelector(selectError);
  const toast = useToast();

  const [filter, setFilter] = useState('');

  useEffect(() => {
    dispatch(fetchContacts());
  }, [dispatch]);

  const handleSubmit = e => {
    e.preventDefault();
    const form = e.target;
    const name = form.elements.name.value.trim();
    const number = form.elements.number.value.trim();

    if (!name || !number) {
      toast({ title: '  ', status: 'warning' });
      return;
    }

    if (contacts.some(c => c.name.toLowerCase() === name.toLowerCase())) {
      toast({ title: '   ', status: 'error' });
      return;
    }

    dispatch(addContact({ name, number }))
      .unwrap()
      .then(() => toast({ title: ' !', status: 'success' }))
      .catch(() => toast({ title: '  ', status: 'error' }));

    form.reset();
  };

  //     
  const handleDelete = (id, name) => {
    dispatch(deleteContact(id))
      .unwrap()
      .then(() => {
        toast({
          title: ` "${name}" `,
          status: 'info',
        });
      })
      .catch(() => {
        toast({ title: '  ', status: 'error' });
      });
  };

  const filteredContacts = contacts.filter(contact =>
    contact.name.toLowerCase().includes(filter.toLowerCase())
  );

  return (
    <Container maxW="lg" mt={10}>
      <Heading mb={6} textAlign="center">
        
      </Heading>

      {/*   */}
      <Box mb={6} p={6} borderWidth="1px" borderRadius="lg" boxShadow="md">
        <form onSubmit={handleSubmit}>
          <VStack spacing={4}>
            <FormControl isRequired>
              <FormLabel></FormLabel>
              <Input name="name" placeholder=" " />
            </FormControl>
            <FormControl isRequired>
              <FormLabel> </FormLabel>
              <Input name="number" placeholder=" " />
            </FormControl>
            <Button type="submit" colorScheme="teal" width="full">
               
            </Button>
          </VStack>
        </form>
      </Box>

      {/*   */}
      <Box mb={6}>
        <FormControl>
          <FormLabel> </FormLabel>
          <Input
            placeholder="   ..."
            value={filter}
            onChange={e => setFilter(e.target.value)}
          />
        </FormControl>
      </Box>

      {/*   */}
      {isLoading && <Spinner />}
      {error && <Text color="red.500">: {error}</Text>}

      <List spacing={3}>
        {filteredContacts.map(contact => (
          <ListItem
            key={contact.id}
            p={3}
            borderWidth="1px"
            borderRadius="md"
            boxShadow="sm"
          >
            <HStack justify="space-between">
              <Text>
                {contact.name}: {contact.number}
              </Text>
              <IconButton
                aria-label=" "
                icon={<DeleteIcon />}
                colorScheme="red"
                onClick={() => handleDelete(contact.id, contact.name)}
              />
            </HStack>
          </ListItem>
        ))}
      </List>
    </Container>
  );
};

export default ContactsPage;
